export class DesignerMaker {
  static makeEmptyDesigner()
  {
    return  {
      fullName: '',
      instagramName: '',
      ownWebsite: false,
      phoneNumber: '',
      numberOfFollowers: 0,
      region: '',
      startingDate: new Date(2016,0,1,0,0,0)
    };
  }
}

export interface Designer {
  fullName: string;
  instagramName: string;
  ownWebsite: boolean;
  startingDate: Date;
  phoneNumber: string;
  region: string;
  numberOfFollowers: number;
}

export interface WebsiteOwner extends Designer {
  websitesName: string;
  websitesURL: string;
}

export function isWebsiteOwner(designer: Designer): designer is WebsiteOwner {
  return designer.ownWebsite;
}
