import { Component, OnInit } from '@angular/core';
import {FlashMessagesService} from 'angular2-flash-messages';
import {Designer, DesignerMaker, isWebsiteOwner} from '../../models/designer';
import {DesignersService} from '../../services/designers.service';

@Component({
  selector: 'app-add-designer',
  templateUrl: './add-designer.component.html',
  styleUrls: ['./add-designer.component.css']
})
export class AddDesignerComponent implements OnInit {

  designer: Designer = DesignerMaker.makeEmptyDesigner();
  websitesName = '';
  websitesURL = '';

  constructor(
    private fms: FlashMessagesService,
    public designerSvc: DesignersService
  ) { }

  ngOnInit() {
    console.log(this.designer.startingDate);
  }

  addDesigner()
  {
    this.tryToAddOwnership();
    this.designerSvc.addDesigner(this.designer).then( () => {
        this.fms.show('עלה בהצלחה!', {cssClass: 'alert-success', timeout: 8000});
      }
    ).catch(err => {
        this.fms.show('חלה בעיה במהלך העלאת המעצב/ת', { cssClass: 'alert-danger', timeout: 3000 });
      }
    )
  }

  isWebsiteOwner() {
    return isWebsiteOwner(this.designer);
  }

  tryToAddOwnership()
  {
    if (isWebsiteOwner(this.designer))
    {
      this.designer.websitesName = this.websitesName;
      this.designer.websitesURL = this.websitesURL;
    }
  }

  isValid()
  {
    return this.designerSvc.isValid(this.designer, this.websitesName, this.websitesURL);
  }

}
